export default function Contact() {
    return (
        <div style={{ padding: "20px", fontFamily: "Arial, sans-serif", backgroundColor: "#121212", minHeight: "100vh", color: "#ffffff" }}>
            <h1 style={{ textAlign: "center", marginBottom: "20px" }}>Contact Me</h1>
            <div style={{ maxWidth: "600px", margin: "0 auto", backgroundColor: "#1e1e1e", padding: "20px", borderRadius: "10px", boxShadow: "0 0 10px rgba(0, 0, 0, 0.5)" }}>
                <form>
                    <div style={{ marginBottom: "15px" }}>
                        <label style={{ display: "block", marginBottom: "5px", color: "#bb86fc" }}>Name:</label>
                        <input type="text" style={{ width: "100%", padding: "10px", borderRadius: "5px", border: "1px solid #333", backgroundColor: "#333", color: "#ffffff" }} />
                    </div>
                    <div style={{ marginBottom: "15px" }}>
                        <label style={{ display: "block", marginBottom: "5px", color: "#bb86fc" }}>Email:</label>
                        <input type="email" style={{ width: "100%", padding: "10px", borderRadius: "5px", border: "1px solid #333", backgroundColor: "#333", color: "#ffffff" }} />
                    </div>
                    <div style={{ marginBottom: "15px" }}>
                        <label style={{ display: "block", marginBottom: "5px", color: "#bb86fc" }}>Message:</label>
                        <textarea style={{ width: "100%", padding: "10px", borderRadius: "5px", border: "1px solid #333", backgroundColor: "#333", color: "#ffffff", height: "150px" }} />
                    </div>
                    <button type="submit" style={{ padding: "10px 20px", backgroundColor: "#bb86fc", color: "#121212", border: "none", borderRadius: "5px", cursor: "pointer" }}>
                        Submit
                    </button>
                </form>
            </div>
        </div>
    );
}